.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  padding: 10px 25px;
  color: #fff;

  text-transform: uppercase;

  background: linear-gradient(#D4145A, #FBB03B);
  background-size: 1px 70px;
  border: none;
  border-radius: 25px;
  cursor: pointer;

  transition: all 0.5s;
}

.button--reverse {
  background-image: linear-gradient(#8E78FF, #FC7D7B);
}

.button:hover {
  background-position: 50px;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;

  text-align: center;
}
